// CUSTOM FONTS

@font-face {
    font-family: 'Merriweather light';
    src: url('./fonts/merriweather-light-webfont.eot');
    src: url('./fonts/merriweather-light-webfont.eot?#iefix') format('embedded-opentype'),
         url('./fonts/merriweather-light-webfont.woff2') format('woff2'),
         url('./fonts/merriweather-light-webfont.woff') format('woff'),
         url('./fonts/merriweather-light-webfont.ttf') format('truetype'),
         url('./fonts/merriweather-light-webfont.svg#merriweatherlight') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Merriweather lightitalic';
    src: url('./fonts/merriweather-lightitalic-webfont.eot');
    src: url('./fonts/merriweather-lightitalic-webfont.eot?#iefix') format('embedded-opentype'),
         url('./fonts/merriweather-lightitalic-webfont.woff2') format('woff2'),
         url('./fonts/merriweather-lightitalic-webfont.woff') format('woff'),
         url('./fonts/merriweather-lightitalic-webfont.ttf') format('truetype'),
         url('./fonts/merriweather-lightitalic-webfont.svg#merriweatherlight_italic') format('svg');
    font-weight: normal;
    font-style: italic;
  }
  
  @font-face {
    font-family: 'Merriweather bold';
    src: url('./fonts/merriweather-bold-webfont.eot');
    src: url('./fonts/merriweather-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('./fonts/merriweather-bold-webfont.woff2') format('woff2'),
         url('./fonts/merriweather-bold-webfont.woff') format('woff'),
         url('./fonts/merriweather-bold-webfont.ttf') format('truetype'),
         url('./fonts/merriweather-bold-webfont.svg#merriweatherbold') format('svg');
    font-weight: bold;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Merriweather bolditalic';
    src: url('./fonts/merriweather-bolditalic-webfont.eot');
    src: url('./fonts/merriweather-bolditalic-webfont.eot?#iefix') format('embedded-opentype'),
         url('./fonts/merriweather-bolditalic-webfont.woff2') format('woff2'),
         url('./fonts/merriweather-bolditalic-webfont.woff') format('woff'),
         url('./fonts/merriweather-bolditalic-webfont.ttf') format('truetype'),
         url('./fonts/merriweather-bolditalic-webfont.svg#merriweatherbold_italic') format('svg');
    font-weight: bold;
    font-style: italic;
  }

@font-face {
    font-family: 'Novecento';
    src: url('./fonts/novecentosanswide-normal-webfont.eot');
    src: url('./fonts/novecentosanswide-normal-webfont.eot?#iefix') format('embedded-opentype'),
         url('./fonts/novecentosanswide-normal-webfont.woff2') format('woff2'),
         url('./fonts/novecentosanswide-normal-webfont.woff') format('woff'),
         url('./fonts/novecentosanswide-normal-webfont.ttf') format('truetype'),
         url('./fonts/novecentosanswide-normal-webfont.svg#novecento_sans_widenormal') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Novecento demibold';
    src: url('./fonts/novecentosanswide-demibold-webfont.eot');
    src: url('./fonts/novecentosanswide-demibold-webfont.eot?#iefix') format('embedded-opentype'),
         url('./fonts/novecentosanswide-demibold-webfont.woff2') format('woff2'),
         url('./fonts/novecentosanswide-demibold-webfont.woff') format('woff'),
         url('./fonts/novecentosanswide-demibold-webfont.ttf') format('truetype'),
         url('./fonts/novecentosanswide-demibold-webfont.svg#novecento_sans_widedemibold') format('svg');
    font-weight: bold;
    font-style: normal;
  }

  body {
     font-family: 'Merriweather light';
     font-size: $base-font-size;
     color: $font-color;
  }

  h1, h2, h3, h4 {
      font-family: 'Novecento demibold';
      text-transform: uppercase;
  }

  h2, h3 {
       letter-spacing: 0.1rem;
  }

  h1 {
       font-size: 2.5rem;
  }

  h2 {
      font-size: 1.8rem;
  }

  h3 {
     font-size: 1.2rem;
 }

 h4 {
      font-size: 1rem;
 }

 h5 {
     font-family: 'Novecento';
     font-size: 1rem;
 }

  a {
     color: $green;
     font-family: 'Novecento';
     text-decoration: none;
     &:hover {
          color: $purple;
     };
  }

  p {
     font-family: 'Merriweather light';
     text-transform: none;
  }
  